html, body {
  font-family: 'Poppins', sans-serif;
  overflow-x: hidden;
  color: $text-gray;
  font-size: 10px;
  background-color: $white;
  @media (min-width: 425px) {
    font-size: 12px;
  }
  @media (min-width: 768px) {
    font-size: 14px;
  }
  @media (min-width: 1200px) {
    font-size: 16px;
  }
}

body {
  padding-right: 0 !important;
}

.fa-stack {
  font-size: 1.2rem;
  .icon-bg {
    color: $gray-dark;
    transition: all 0.3s;
  }
  .icon-red {
    color: $white;
    transition: all 0.3s;
  }
}

.redes-celeste {
  text-decoration: none;
  margin: 0 1rem;
  .fa-stack {
    font-size: 2.2rem;
    .icon-bg {
      color: $celeste;
      transition: all 0.3s;
    }
    .icon-red {
      color: $white;
      transition: all 0.3s;
    }
  }
  &:hover {
    .icon-bg {
      color: $gray-dark;
    }
    .icon-red {
      color: $white;
    }
  }
}

.redes-gray {
  text-decoration: none;
  margin: 0 .2rem;
  .fa-stack {
    font-size: 1.6rem;
    .icon-bg {
      color: $gray-dark;
      transition: all 0.3s;
    }
    .icon-red {
      color: $white;
      font-size: 1.3em;
      transition: all 0.3s;
    }
  }
  &:hover {
    .icon-bg {
      color: $celeste;
    }
    .icon-red {
      color: $white;
    }
  }
}
